import React, {PureComponent} from 'react'
import {
  Table
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.outNumGather, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '商品编码',
      width: 150,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '商品名称',
      width: 200,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '单位',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '缺货量',
      width: 100,
      dataIndex: 'shortageQty',
      key: 'shortageQty'
    }, {
      title: '出库地点',
      width: 100,
      dataIndex: 'sendLocationCode',
      key: 'sendLocationCode'
    }, {
      title: '商品大类',
      width: 150,
      dataIndex: 'bigCategoryName',
      key: 'bigCategoryName'
    }, {
      title: '中类',
      width: 150,
      dataIndex: 'centreCategoryName',
      key: 'centreCategoryName'
    }, {
      title: '小类',
      width: 150,
      dataIndex: 'smallCategoryName',
      key: 'smallCategoryName'
    }]
  }

  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value) return
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}})
    }
  }
  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size, tableActiveKey} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({searchParams: {page, size, receiveStatus: tableActiveKey, ...searchParams, isAsc: true, columns: sorter.field}}, true)
      } else {
        handleGetList({searchParams: {page, size, receiveStatus: tableActiveKey, ...searchParams, isAsc: false, columns: sorter.field}}, true)
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({searchParams: {page: pagination.current, size: pagination.pageSize, receiveStatus: tableActiveKey, ...searchParams}}, true)
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={(record, index) => index}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
        />
      </div>
    )
  }
}



