import * as actionType from './actionType'
import {getData} from 'util'
import {API_SHIPPEDORDERS_SHORTAGE_STATISTIC_PAGE, API_SHIPPEDORDERS_SHORTAGE_STATISTIC_EXPORT} from 'api'
import moment from 'moment'

//同步
export const update = (object) => ({
  type: actionType.OUTNUMGATHER_UPDATE,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.OUTNUMGATHER_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      i === 'productName' ? reqObj[i] = reqObj[i].children : reqObj[i] = reqObj[i].value
      // reqObj[i] = reqObj[i].value
    }
  }
  getData({api: API_SHIPPEDORDERS_SHORTAGE_STATISTIC_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.OUTNUMGATHER_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [], // 选中的队列数据
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.OUTNUMGATHER_FILE
    })
  })
}

// 导出
export const handleExport = (reqData, callBack) => (dispatch) => {
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  getData({api: API_SHIPPEDORDERS_SHORTAGE_STATISTIC_EXPORT, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.OUTNUMGATHER_FILE
    })
  })
}



