import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  Input
  // Checkbox
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect, CsxMultipleSelect, CsxMultipleSearch} from 'Form'
import {API_PRODUCTS_KEYVALUES, API_PRODUCT_CATEGORYSONS, API_MERCHANTPRIORITYCHOICE_KEYVALUES, API_USER_WAREHOUSELIST, API_SHIPPEDORDERS_BUSINESSKEYVALUES, API_PRODUCT_GETPRODUCTKEYVALUES} from 'api'

@connect(
  state => ({...state.outNumGather, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 重置
  reset = () => {
    const {update, userLoation} = this.props
    update({searchParams: {warehouseCode: {value: userLoation.value, children: userLoation.value}}})
  }

  render() {
    const {
      searchParams: {
        warehouseCode,
        bigCategoryCode,
        centreCategoryCode,
        smallCategoryCode,
        productCode,
        shopType,
        businessType,
        orderCode,
        customerName,
        shopCode
      },
      userLoation
    } = this.props
    console.log(warehouseCode, 111)
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={8}>
            <CsxMultipleSelect
              dateApi={API_USER_WAREHOUSELIST}
              label='查询地点'
              selectItem={{data: warehouseCode ? warehouseCode : {value: userLoation.value, children: userLoation.value}, key: 'warehouseCode', valueKey: ['warehouseCode', 'warehouseName']}}
              onChangeSelect={(value) => {
                this.onInputChange({warehouseCode: value})
              }}
            />
          </Col>
          <Col span={16}>
            <Row type="flex">
              <CsxMultipleSelect
                dateApi={API_PRODUCTS_KEYVALUES}
                label='商品分类'
                placeholder='大类'
                searchParams={{type: '2'}}
                selectItem={{data: bigCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({bigCategoryCode: value})
                }}
                style={{width: 163}}
              />
              <CsxMultipleSelect
                placeholder='中类'
                dateApi={API_PRODUCT_CATEGORYSONS}
                searchParams={{parCode: bigCategoryCode && bigCategoryCode.value, level: '3'}}
                selectItem={{data: centreCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({centreCategoryCode: value})
                }}
                noReady
                errMessage='请先选择大类'
                style={{marginLeft: 10, width: 163}}
              />
              <CsxMultipleSelect
                placeholder='小类'
                dateApi={API_PRODUCT_CATEGORYSONS}
                searchParams={{parCode: centreCategoryCode && centreCategoryCode.value, level: '4'}}
                selectItem={{data: smallCategoryCode, key: 'categoryCode', valueKey: 'categoryName'}}
                onChangeSelect={(value) => {
                  this.onInputChange({smallCategoryCode: value})
                }}
                noReady
                errMessage='请先选择中类'
                style={{marginLeft: 10, width: 163}}
              />
            </Row>
          </Col>
          {/* <Col span={8}>
            <CsxSelect
              dateApi={API_MERCHANTPRIORITYCHOICE_KEYVALUES}
              label='门店类型'
              selectItem={{data: shopType, key: 'merchantType', valueKey: 'merchantType'}}
              onChangeSelect={(value) => {
                this.onInputChange({shopType: value})
              }}
            />
          </Col>
          <Col span={8}>
            <CsxSelect
              // dateApi={API_SHIPPEDORDERS_BUSINESSKEYVALUES}
              data={[{key: '26', value: '寄售POS销售'}, {key: '27', value: '自营POS销售'}]}
              label='业务类型'
              searchParams={{orderType: 'S'}}
              selectItem={{data: businessType, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({businessType: value})
              }}
            />
          </Col>
          <Col span={8}>
            <div className='commonInput'>
              <label>订单号：</label>
              <div className='box'>
                <Input placeholder="批量查询编码用,号隔开" value={orderCode} onChange={(e) => {
                  this.onInputChange({orderCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col> */}
          {/* <Col span={8}>
            <div className='commonInput'>
              <label>客户：</label>
              <div className='box'>
                <Input placeholder="批量查询编码用,号隔开" value={customerName} onChange={(e) => {
                  this.onInputChange({customerName: e.target.value})
                }}/>
              </div>
            </div>
          </Col> */}
          {/* <Col span={8}>
            <div className='commonInput'>
              <label>门店编号：</label>
              <div className='box'>
                <Input placeholder="批量查询编码用,号隔开" value={shopCode} onChange={(e) => {
                  this.onInputChange({shopCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col> */}
          <Col span={8}>
            {/* <CsxMultipleSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label='商品编码'
              searchParamsKey='productCode'
              searchParams={{warehouseCode: (warehouseCode && warehouseCode.value) || userLoation.value}}
              selectItem={{data: productCode, key: 'key', valueKey: ['key', 'value']}}
              onSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            /> */}
            <div className='commonInput'>
              <label>商品编码：</label>
              <div className='box'>
                <Input placeholder="批量查询编码用,号隔开" value={productCode} onChange={(e) => {
                  this.onInputChange({productCode: e.target.value})
                }}/>
              </div>
            </div>
          </Col>
          <Col span={16}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} >查询</Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} >重置</Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



