import React, {PureComponent} from 'react'
import {
  message,
  Button
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
// import {API_CAISX_B2B_WMS_INVENTORYMULTIPLELOCATIONIMPORT} from 'api'
// import {Import} from 'HOC'

@connect(
  state => ({...state.outNumGather, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  handleExport = () => {
    const {handleExport, searchParams, userLoation, page, size} = this.props
    handleExport({searchParams: {page, size, ...searchParams, productName: searchParams.productName ? searchParams.productName.children : ''}, userLoation}, () => {
      message.success('导出成功')
    })
  }
  /* 下载模版 */
  handleDownLoad = (key) => {
    const downloadData = JSON.parse(localStorage.getItem('b2bWmsDownload'))
    window.open(downloadData[key].downloadUrl)
  }

  /* 导入成功 */
  handleImportSuccess = res => {
    const {handleGetList, searchParams, page, size, update} = this.props
    res && res.message === 'ok' && update({resultModalDataList: res.result.data, isShowResultModal: true})
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  /* 报错导出 */
  handleExprotError = () => {
  }
  render() {
    return (
      <div className={style['table-tool']}>
        {/* <Button type="primary" className={style.btn} onClick={() => {
          // this.handleDownLoad('T03')
        }}>模板下载</Button>
        <Import
          action={{api: API_CAISX_B2B_WMS_INVENTORYMULTIPLELOCATIONIMPORT}}
          onOk={this.handleImportSuccess}
          onExprotError={this.handleExprotError}
          beforeImport={() => {
            const {update} = this.props
            update({tableLoading: true})
          }}
          onResponse={() => {
            const {update} = this.props
            update({tableLoading: false})
          }}
        >
          <Button type="primary" className={style.btn}>导入</Button>
        </Import> */}
        <Button type="primary" className={style.btn} onClick={() => {
          this.handleExport()
        }}>导出</Button>
      </div>
    )
  }
}



